// ãƒ•ã‚§ã‚¤ã‚¯AJAXé€šä¿¡whatever..
const rf_wait = () => {
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      resolve('wait')
    }, 2000)
  })
}

// ãƒ¢ãƒ¼ãƒ€ãƒ«å‡¦ç†
const rf_test = () => {

  // ãƒ¢ãƒ¼ãƒ€ãƒ«ã‚’é–‹ããƒœã‚¿ãƒ³å–å¾—
  const modalOpenBtn = document.querySelector('#ajax-modal-test')

  // ãƒ¢ãƒ¼ãƒ€ãƒ«ã‚’é–‹ãå‡¦ç†
  modalOpenBtn.addEventListener('click', async (e) => {    
    e.preventDefault()

    // ãƒ­ãƒ¼ãƒ‰ä¸­ã¯ã‚¯ãƒªãƒƒã‚¯ç„¡åŠ¹åŒ–
    if(e.target.classList.contains('--is-loading')) return

    // ãƒœã‚¿ãƒ³ã‚’ãƒ­ãƒ¼ãƒ‰ä¸­ã«disabled
    e.target.classList.add('--is-loading')

    // ãƒ­ãƒ¼ãƒ€ãƒ¼ã‚’æŒ¿å…¥(ç”»é¢ä¸­å¤®ã«é…ç½®ã™ã‚‹ã‚¹ã‚¿ã‚¤ãƒ«ã‚’æŒ‡å®š)
    const loader = new RF_GLOBALS.RfLoader({
      style: '--bg-black --fixed-center',
    })

    // 2ç§’å¾…ã¤(ãƒ•ã‚§ã‚¤ã‚¯AJAXé€šä¿¡)
    const res = await rf_wait()
    console.log(res)

    // ãƒ­ãƒ¼ãƒ€ãƒ¼ã‚’å‰Šé™¤
    loader.removeLoader()

    // ãƒœã‚¿ãƒ³ã‹ã‚‰ãƒ­ãƒ¼ãƒ‡ã‚£ãƒ³ã‚°ã‚¯ãƒ©ã‚¹ã‚’å‰Šé™¤
    e.target.classList.remove('--is-loading')

    // ãƒ¢ãƒ¼ãƒ€ãƒ«ã‚’é–‹ã
    RF_GLOBALS.rf_modals.openModal('#sys-dialog-area')

  })

}

export default rf_test